<template>
  <div>
    <div class="DNF">
      <el-button type="primary"  @click="addMsg()">添加通知</el-button>
    </div>
    <el-table
      :data="tableData"
      border
      style="width: 80%;margin: 0 auto">
      <el-table-column
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        prop="title"
        label="标题"
        width="160">
      </el-table-column>
      <el-table-column
        prop="author"
        label="发布单位"
        width="160">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="时间"
        width="160">
      </el-table-column>
      <el-table-column
        label="内容">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="showMsg(scope.row)">详情</el-button>
          <el-button type="info" size="mini" @click="editorMsg(scope.row._id)">编辑</el-button>
          <el-button type="danger" size="mini" @click="delMsg(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      title="通知内容"
      :visible.sync="dialogVisible"
      width="30%">
      <span>{{item.content}}</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false" type="primary">关闭</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "list",
    data() {
      return {
        tableData: [],
        dialogVisible: false,
        item:{}
      }
    },
    methods: {
      addMsg(){
        this.$router.push("/notice/add")
      },
      editorMsg(val){
        this.$router.push({path:"/notice/editor",query:{id:val}})
      },
      showMsg(val){
        this.item=val;
        this.dialogVisible=true;
      },
      getMsg() {
        this.$axios.post("getNotices").then(res => {
          this.tableData = res.data;
        })
      },
      delMsg(val) {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios.post("delNotices",{id:val}).then(res => {
            if (res.code === 200) {
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              this.getMsg()
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      }
    },
    created() {
      this.getMsg()
    }
  }
</script>

<style scoped>
.DNF{
  width: 80%;
  margin: 0 auto 10px;
  text-align: right;
}
</style>



